/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 85%;
  max-width: 1400px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

body {
  background-color: #674baf;
}

.seccion__fondo {
  background-image: url(../img/bg-mobile.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 48rem;
}
@media (min-width: 768px) {
  .seccion__fondo {
    background-image: url(../img/bg-desktop.svg);
    background-size: contain;
    height: 98rem;
  }
}
.seccion__logo {
  padding-top: 2rem;
  width: 15rem;
  padding-bottom: 8rem;
}
@media (min-width: 768px) {
  .seccion__logo {
    width: 30rem;
    padding-top: 7rem;
  }
}
@media (min-width: 768px) {
  .seccion__imagen {
    width: 85rem;
  }
}
.seccion__heading {
  margin-top: 5rem;
  color: #FFFFFF;
  text-align: center;
  font-size: 3.5rem;
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
}
@media (min-width: 768px) {
  .seccion__heading {
    text-align: left;
    font-size: 5rem;
  }
}
.seccion__texto {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.8rem;
  line-height: 1.7;
  font-family: "Poppins", sans-serif;
}
@media (min-width: 768px) {
  .seccion__texto {
    text-align: left;
    font-size: 2.2rem;
  }
}
.seccion__boton {
  text-align: center;
  margin: 3.5rem 0 5rem 0;
}
@media (min-width: 768px) {
  .seccion__boton {
    text-align: left;
  }
}
.seccion__boton__btn {
  padding: 1.2rem 8rem;
  background-color: #FFFFFF;
  border-radius: 2.5rem;
}
.seccion__boton__btn:hover {
  background-color: #e882e8;
  color: #FFFFFF;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .seccion__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
  }
}

.redes {
  margin-top: 10rem;
  margin-bottom: 5rem;
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .redes {
    margin-top: 20rem;
    justify-content: flex-end;
  }
}
.redes__logos {
  width: 3rem;
  background-color: white;
  border-radius: 2rem;
}
.redes__logos:hover {
  cursor: pointer;
  background-color: #e882e8;
  transition: 0.4s;
}

.autor {
  color: #FFFFFF;
  font-size: 1.5rem;
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .autor {
    font-size: 2rem;
  }
}