@use 'base/mixins' as m;
@use 'base/variables' as v;

body{
    background-color: v.$primario;
}

.seccion{
    &__fondo{
        background-image: url(../img/bg-mobile.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        height: 48rem;
        @include m.tablet{
            background-image: url(../img/bg-desktop.svg);
            background-size: contain;
            height: 98rem;
        }
    }
    &__logo{
        padding-top: 2rem;
        width: 15rem;
        padding-bottom: 8rem;
        @include m.tablet{
            width: 30rem;
            padding-top:7rem ;
        }
    }
    &__imagen{
        @include m.tablet{
            width: 85rem;
        }
    }

    &__heading{
        margin-top: 5rem;
        color: v.$blanco;
        text-align: center;
        font-size: 3.5rem;
        line-height: 1.5;
        font-family: v.$fuente_principal;
        @include m.tablet{
            text-align: left;
            font-size: 5rem;
        }
    }
    &__texto{
        color: v.$blanco;
        text-align: center;
        font-size: 1.8rem;
        line-height: 1.7;
        font-family: v.$fuente_secundario;
        @include m.tablet{
            text-align: left;
            font-size: 2.2rem;
        }
    }
    &__boton{
        text-align: center;
        margin: 3.5rem 0 5rem 0;
        @include m.tablet{
            text-align: left;
        }
        &__btn{
            padding: 1.2rem 8rem;
            background-color: v.$blanco;
            border-radius: 2.5rem;
            &:hover{
                background-color: v.$secundario;
                color: v.$blanco;
                transition: .3s;
            }
        }
    }
    &__grid{
        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(2,1fr);
            justify-content: center;
            align-items: center;
        }
    }
}

.redes{
    margin-top: 10rem;
    margin-bottom: 5rem;
    display: flex;
    flex-direction: row;
    gap: 2rem;
    justify-content: center;
    @include m.tablet{
        margin-top: 20rem;
        justify-content: flex-end;
    }
    &__logos{
        width: 3rem;
        background-color: white;
        border-radius: 2rem;
        &:hover{
            cursor: pointer;
            background-color: v.$secundario;
            transition: .4s;
        }
    }
}

.autor{
    color: v.$blanco;
    font-size: 1.5rem;
    text-align: center;
    margin: 0;
    @include m.tablet{
        font-size: 2rem;
    }
}